<style>
  @import "../../../../../static/style/file_upload.css";
  .mh-up-w {
    float: left;
    margin-left: 10px;
  }
  .mh-up-w:first-child {
    margin-left: 0;
  }
  .mh-up-h-w {
    padding: 0 5px;
    line-height: 0;
    margin-top: -8px;
    margin-bottom: 8px;
    text-align: center;
  }
  .up-h-i {
    cursor: pointer;
    padding-left: 6px;
    padding-right: 6px;
  }
  .up-h-i:hover {
    color: #3080fe;
  }
</style>
<template>
  <div class="add-form-wraper">
    <el-form ref="form" :model="addParamMesg" label-width="160px">
      <region label="选择区域" className="add-input" :required="true"></region>
      <el-form-item label="是否为供应商专供">
        <el-select class="add-input" 
          v-model="addParamMesg.vendorType" 
          filterable 
          placeholder="是否为供应商专供"
          size="small">
          <el-option
            v-for="item in vendorTypeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="选择商品" required>
        <el-select
          v-model="goodsQueryVal"
          filterable
          remote
          size="small" 
          class="add-input"
          reserve-keyword
          placeholder="请输入SKU编码/商品名称关键词搜索"
          :remote-method="queryGoodsSearchAsync"
          :loading="queryGoodsAsyncLoading"
          @change="chooseGoodsChangeHandle">
          <el-option
            v-for="item in queryGoodsSearchAsyncList"
            :key="item.goodsId"
            :label="item.goodsNametemp"
            :value="item.goodsId">
          </el-option>
        </el-select>
        <span>请输入SKU编码/商品名称关键词,在搜索到的数据中选择所需的SKU</span>
      </el-form-item>
        
      <el-form-item label="商品名称" required>
        <el-input v-model="addParamMesg.goodsName" size="small" class="add-input" placeholder="请输入商品名称"></el-input>
        <span>商品名称,限制30个汉字</span>
      </el-form-item>

      <el-form-item label="规格">
        <el-input v-model="addParamMesg.spec" size="small" class="add-input" disabled></el-input>
      </el-form-item>

      <el-form-item label="区域售价" required>
        <el-input v-model="addParamMesg.price" size="small" class="add-input" placeholder="请输入区域售价">
          <el-button slot="append">元</el-button>
        </el-input>
        <span>区域售价仅为初始售价，不可修改，商品售价以门店售价为准</span>
      </el-form-item>
      <el-form-item label="商品大图">
        <el-upload
          class="add-sf-goods-img"
          action=""
          :show-file-list="false"
          :before-upload="imageUploadReg"
          :http-request="emitUpload">
          <div v-loading="imageUploadLoading">
            <img v-if="addParamMesg.bigImageUrl" :src="addParamMesg.bigImageUrl" class="add-avatar-sf-goods">
            <i v-else class="el-icon-plus add-sf-goods-icon"></i>
          </div>
        </el-upload>
        <span>建议上传大小不超过300K的710*420像素的jpg或png图片</span>
      </el-form-item>
      <el-form-item label="商品检测报告">
        <div>
          <draggable v-model="imageListArr" @update="datadragEnd">
            <transition-group>
              <div class="mh-up-w" v-for="(item, idx) of imageListArr" @click="setItemIdx(idx)" :key="item.imgUrl">
                <el-upload
                  class="avatar-uploader"
                  action=""
                  :show-file-list="false"
                  :before-upload="imageUploadRegForMore"
                  :http-request="emitUploadForMore">
                  <div v-loading="detailImageUploadLoading && currentUpFileIdx == idx">
                    <img v-if="item.imgUrl" :src="item.imgUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  </div>
                </el-upload>
                <div class="mh-up-h-w" v-if="item.imgUrl && item.imgUrl != ''"> 
                  <span class="up-h-i" @click="deleteUpFile(idx)"><i class="el-icon-delete"></i></span>
                  <span class="up-h-i"><i class="el-icon-rank"></i></span>
                </div>
              </div>
            </transition-group>
          </draggable>
          <div class="tip-txt-w">
            <span>建议上传宽度为750像素的jpg或png图片，大小不能超过1M，最多可上传6张</span>
          </div> 
        </div>
      </el-form-item>
      <hr class="s-slip-tb">
      <el-form-item>
        <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled" :icon="isDisabled ? 'el-icon-loading' : ''">保存</el-button>
        <el-button size="small" @click="doCancelHandel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import Region from '@/components/Common/StoreCommon/Region'
  import Storage from '@/utils/localStorage'
  import Draggable from 'vuedraggable'
  import * as utils from '@/utils/utils'
  import * as qiniu from 'qiniu-js'
  const fileUrlList = {
    getQiNiuToken: '/foundation/qiniu/auth/file'
  }
  const presaleGoodsMgt = {
    // 添加预售商品-选择商品
    searchGoods: '/plateform/a/platformCityGoods/presale/add/searchGoods',
    // 添加/编辑预售商品-保存操作
    presaleSave: '/plateform/a/platformCityGoods/presale/save'
  }
  export default {
    data() {
      return {
        imageUploadLoading: false,
        isDisabled: false, // 保存防止多次点击
        // 添加内容
        addParamMesg: {
          DIFFCITY: true,
          goodsId: '', // 商品ID
          goodsName: '', // 预售商品名称
          price: '', // 原价/售价
          saleType: 4, // 售卖类型：1：常规；2：预售；3:长周期预售，4：及时达(查询对应售卖类型数据该字段必传，不传入择默认全部)
          platformId: 5,
          spec: '', // 规格
          bigImageUrl: '',
          regionCode: Storage.get('regionCode') || '', // 区域ID
          cityGoodsImgReqs: [],
          vendorType: 2
        },
        goodsQueryVal: '', // 输入的内容
        queryGoodsAsyncLoading: false,
        // 商品选择
        queryGoodsSearchAsyncList: [],
        // 商品图临时使用  imgUrl: 图片地址  sortNum: 序号 goodsId: 商品编码 
        imageListArr: [
          {
            imgUrl: '',
            sortNum: '',
            imgType: 22
          }
        ],
        detailImageUploadLoading: false,
        currentUpFileIdx: -1,
        vendorTypeOptions: [
          {
            label: '是',
            value: 1
          },
          {
            label: '否',
            value: 2
          }
        ]
      }
    },
    components: {
      "region": Region,
      'draggable': Draggable
    },
    methods: {
      /**
       * [queryGoodsSearchAsync 远程选择商品]
       * @param  {[type]} queryString [description]
       * @return {[type]}             [description]
       */
      queryGoodsSearchAsync(queryString) {
        if(queryString === '') {
          return
        }
        let _params = {
          DIFFCITY: true,
          regionCode: this.addParamMesg.regionCode,
          saleType: this.addParamMesg.saleType,
          searchValue: queryString
        }
        this.queryGoodsAsyncLoading = true
        this.$http.post(presaleGoodsMgt.searchGoods, _params).then(res => {
          this.queryGoodsAsyncLoading = false
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                item.goodsNametemp = item.goodsNo + '-' + item.goodsName + '-' + item.spec
              }
              this.queryGoodsSearchAsyncList = res.data
            }else {
              this.queryGoodsSearchAsyncList = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.queryGoodsAsyncLoading = false
          console.log("提示(远程搜索商品)：", e.message)
        })
      },
      /**
       * [chooseGoodsChangeHandle 当搜索到的商品切换选择时触发]
       * @param  {[type]} item [description]
       * @return {[type]}      [description]
       */
      chooseGoodsChangeHandle(item) {
        let checkedItem = this.queryGoodsSearchAsyncList.filter(j => {
          return j.goodsId == item
        })[0]

        this.addParamMesg.goodsId = checkedItem.goodsId
        this.addParamMesg.goodsName = checkedItem.goodsName
        this.addParamMesg.skuNo = checkedItem.skuNo
        this.addParamMesg.spec = checkedItem.spec

      },
      /**
       * [doSaveHandel 保存操作]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        if(this.addParamMesg.goodsId === "" || this.addParamMesg.goodsName === "") {
          this.$message("请选择商品")
          return
        }

        if(this.addParamMesg.goodsName === "") {
          this.$message("请输入商品名称")
          return
        }else {
          if(this.addParamMesg.goodsName.length > 30) {
            this.$message("商品名称,限制30个汉字")
            return
          }
        }

        if(this.addParamMesg.price == "") {
          this.$message("请输入售价")
          return 
        }else {
          if(!salePriceReg.test(this.addParamMesg.price)) {
            this.$message('售价是大于0，小数保留小数点后2位') 
            return
          }
        }

        // if(!this.addParamMesg.bigImageUrl) {
        //   this.$message('请上传商品大图') 
        //   return
        // }
        // 检测报告图片
        this.addParamMesg.cityGoodsImgReqs = this.imageListArr.filter(item => {
          return item.imgUrl
        })
        this.isDisabled = true
        this.$http.post(presaleGoodsMgt.presaleSave, {...this.addParamMesg}).then(res => {
          // 保存按钮可再次点击
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$router.push('/timely_goods')
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doCancelHandel 取消操作]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$confirm('确定取消添加商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/timely_goods')
        }).catch(() => {})
      },
      /**
       * [getToken 通过后台封装的接口获取七牛的token]
       * @return {[type]}            [description]
       */
       getToken () {
        return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
          if (res.code == 0) {
            let token = res.data.token
            let fileName = res.data.key
            return {fileName, token}
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.imageUploadLoading = false
          this.$notify.error({
            title: '获取七牛token提示',
            message: e.message
          })
        })
      },
      /**
       * [imageUploadReg 上传图片检测]
       * @param  {[type]} file [description]
       * @return {[type]}      [description]
       */
       imageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        const isSizeNum = file.size <= 1 * 300 * 1024
        if (!isPic || !isJPG || !isSizeNum) {
          this.$message.error('商品大图建议上传大小不超过300K的710*420像素的jpg或png图片')
        }
        return isPic && isJPG && isSizeNum
      },
      /**
       * [emitUpload 上传图片]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      emitUpload(e) {
        this.imageUploadLoading = true
        this.getToken().then((res) => {
          // 开始上传图片
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.imageUploadLoading = false
            },
            complete: (res) => {
              // 添加内容设置图片地址
              let urlImage = 'http://retailimg01.benlailife.com/' + res.key
              this.imageUploadLoading = false
              // 设置对应的图片地址
              this.addParamMesg.bigImageUrl = urlImage
            },
            next: res => {
            }
          })
        })
      },
      setItemIdx(idx) {
        if(this.detailImageUploadLoading) return
        this.currentUpFileIdx = idx
      },
      deleteUpFile(_idx) {
        this.imageListArr = this.imageListArr.filter((item, idx) => {
          return idx != _idx
        })
      },
      /**
       * [datadragEnd 拖拽结束]
       * @return {[type]} [description]
       */
      datadragEnd(evt) {
        this.imageListArr.forEach((item, index) => {
          item.sortNum = index + 1
        })
        this.imageListArr = this.imageListArr.filter(item => {
          return item.imgUrl != ''
        });
        let imageListItem = {
          imgType: 22,
          imgUrl: '',
          sortNum: ''
        }
        this.imageListArr.push(imageListItem)
      },
      imageUploadRegForMore (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        const isSizeNum = file.size <= 1 * 1024 * 1024
        if (!isPic) {
          this.$message.error('建议上传宽度为750像素的jpg或png图片，大小不能超过1M，最多可上传6张')
          return false
        }
        if (!isJPG) {
          this.$message.error('建议上传宽度为750像素的jpg或png图片，大小不能超过1M，最多可上传6张')
          return false
        }
        if (!isSizeNum) {
          this.$message.error('建议上传宽度为750像素的jpg或png图片，大小不能超过1M，最多可上传6张')
          return false
        }
        return isPic && isJPG && isSizeNum
      },
      emitUploadForMore(e) {
        if(this.imageListArr.length > 6 && this.currentUpFileIdx != -1) {
          this.$message.error('商品检测报告，最多可添加6张')
          return
        }
        this.detailImageUploadLoading = true
        this.getToken().then((res) => {
          // 开始上传图片
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.detailImageUploadLoading = false
            },
            complete: (res) => {
              // 添加内容设置图片地址
              let urlImage = 'http://retailimg01.benlailife.com/' + res.key
              this.detailImageUploadLoading = false
              // 设置对应的图片地址
              this.setImgUrl(this.currentUpFileIdx, urlImage)

              if(this.currentUpFileIdx != -1) {
                let hash = {}
                let imageListItem = {
                  imgType: 22,
                  imgUrl: '',
                  sortNum: ''
                }
                this.imageListArr.push(imageListItem)

                // 去重  防止多个空对象
                this.imageListArr = this.imageListArr.reduceRight((item, next) => {
                  hash[next.imgUrl] ? '' : hash[next.imgUrl] = true && item.push(next)
                  return item
                }, [])
                this.imageListArr.reverse()
              }

            },
            next: res => {
            }
          })
        })
      },
      setImgUrl(_idx, _imgUrl) {
        let urlImage = _imgUrl
        let currentIdx = _idx
        this.imageListArr.forEach((item, index) => {
          if(index === currentIdx) {
            this.imageListArr[index].imgUrl = urlImage
            this.imageListArr[index].sortNum = parseInt(currentIdx) + 1
          }
        })
      }
    }
  }
</script>


